.infoScreenContainerHidden
{
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #0008;
  opacity: 0;
  z-index: -999;
  transition: opacity 0.7s ease-in;
}


.infoScreenContainerVisible
{
  overflow-y: scroll;
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #0008;
  opacity: 1;
  z-index: 999;
  transition: opacity 0.3s ease;
}


.infoScreen
{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 8vh 0;
  padding: 2vh 1vw;
  background-color: var(--supplementary-color-2);
  border-radius: 10px;
  min-width: 10vh;
  height: fit-content;
  opacity: 1;
  transition: opacity 0.2s ease-in;
}


.infoScreenHidden
{
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 2vh 1vw;
  background-color: var(--supplementary-color-2);
  border-radius: 10px;
  min-width: 10vh;
  min-height: 10vw;
  opacity: 0;
  transition: opacity 0s ease-in;
}
